#logo{
    position: absolute;
    margin: 0px;
    top: 0px;
    left: 0px;
    display: block;
    width: @logo-width;
    height: 0px;
    padding-top: @logo-height;
    background-image: url(../img/logo.png);
    background-size: @logo-width @logo-height;
    overflow: hidden;
    pointer-events: none;
}

//media queries
@media only screen and (max-width: 900px) {
    #logo{
        width: @logo-width * 0.75;
        padding-top: @logo-height * 0.75;
        background-size: @logo-width * 0.75 @logo-height * 0.75;
    }
}

@media only screen and (max-width: 768px){
    #logo{
        width: @logo-width * 0.5;
        padding-top: @logo-height * 0.5;
        background-size: @logo-width * 0.5 @logo-height * 0.5;
    }
}

@media only screen and (max-height: 500px){
    #logo{
        width: @logo-width * 0.25;
        padding-top: @logo-height * 0.25;
        background-size: @logo-width * 0.25 @logo-height * 0.25;
    }
}