.thumbnail-button{
    position: relative;
    display: inline-block;
    cursor: pointer;
    -webkit-tap-highlight-color: transparent;
    
    img{
        display: block;
    }
    
    &.grayedOut img{
        opacity: 0.5;
    }
    
    //state styles for when this thumbnail is selected
    &.selected{
        img{
            background: fade(@brand-interactive, 50%);
        }
    }

    &.no-onclick{
        cursor: default;
    }
    
    &.labelType-labelOnly{
        padding: 0.1em 0.4em;
        display: inline-block;
        color: #f1f1f1;
        border: 1px solid fade(#f1f1f1, 50%);
        background: rgba(0,0,0,0.7);
        margin: 0.3em 0em 0em 0.3em;
        position: relative;
        
        &.selected{
            background-color: fade(@brand-interactive, 50%);
            border: 1px solid #f1f1f1;
        }
        &.grayedOut{
            color: fade(#f1f1f1, 50%);
            border: 1px dotted fade(#f1f1f1, 50%);
        }
        &:hover{
            border: 1px solid #f1f1f1;
            color: #f1f1f1;
        }
    }


    //styles for color palette swatches
    &.paint-preconf{
        display: inline-flex;
        overflow: hidden;
        border-radius: 5px;
        margin: 1em;

        //rules for each embedded swatch
        span{
            flex: 2;
        }

        img{
            width: 100%;
            height: 100%;
        }

        &.missing-swatches{
            background: url("/static/img/textures/question-mark-gray.png");
            background-size: 32px 32px;
            text-shadow: 0px 1px 1px rgba(0,0,0,0.4);
        }

        //data said it's 120x120 but it really should be 60x60...
        &.w120h120{
            width: 60px;
            height: 60px;
        }
        &.w32h32{
            width: 32px;
            height: 32px;
        }

        //specific styles for swatches
        &.paint-swatch{
            margin: 4;
            vertical-align: middle;
        }
    }
    
    //tag for "new" parts / configs / thumbnails
    .new-tag{
        position: absolute;
        top: 0px;
        left: 50%;
        line-height: 1em;
        font-size: 0.5em;
        .translate(-50%, 0%);
        
        .tag-text
        {
            display: block;
            text-align: center;
            background: @brand-primary;
            line-height: 0.9em;
            padding-top: 1px;
            font-family: 'Roboto', sans-serif;
        }
        .arrow-down
        {
            width: 0;
            height: 0;
            border-left: 12px solid transparent;
            border-right: 12px solid transparent;
            border-top: 2px solid @brand-primary;
            content: "";
            display: block;
        }
    }
}