.modal{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 3;
    
    .modal-bg{
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0px;
        left: 0px;
        background: rgba(0,0,0,0.7);
    }
    .modal-close{
        position: absolute;
        top: -0.5em;
        right: -0.5em;
        background: rgb(171,0,7);
        color: #f1f1f1;
        padding: 0em;
        text-align: center;
        width: 1.2em;
        height: 1.3em;
        cursor: pointer;
    }
    .modal-window-wrapper{
        position: absolute;
        width: 100%;
        height: 100%;
        .flexbox();
        .flexbox-align-items(center);
        justify-content: center;
    }
    .modal-window{
        position: relative;
        // left: 50%;
        // top: 50%;
        color: #f1f1f1;
        // -webkit-transform: translate(-50%,-50%);
        // -moz-transform: translate(-50%,-50%);
        // -o-transform: translate(-50%,-50%);
        // -ms-transform: translate(-50%,-50%);
        // transform: translate(-50%,-50%);
        max-width: 100%;
        max-height: 100%;
        
        #basket-wrapper {
            width: 900px;
            height: 650px;
            background: #f1f1f1;
            background-image: url(../img/ajax-loader.gif);
            background-repeat: no-repeat;
            background-position: center;
            max-width: 100%;
            max-height: 100%;
            
            iframe {
                width: 100%;
                height: 100%;
                border: none;
                outline: none;
            }
        }
    }
}

.modal-fade-enter {
  opacity: 0.01;
}

.modal-fade-enter.modal-fade-enter-active {
  opacity: 1;
  .transition-single(opacity 300ms ease-in);
}

.modal-fade-leave {
  opacity: 1;
}

.modal-fade-leave.modal-fade-leave-active {
  opacity: 0.01;
  .transition-single(opacity 300ms ease-out);
}

@media screen and (max-width: 910px) , screen and (max-height: 700px) {
  .modal .modal-close{
        top: 0;
        right: 0;
    }
}