.open-voucher{
    cursor: pointer;
    
    img{
        vertical-align: top;
    }
}

#voucher-wrapper{
    background: rgba(0,0,0,0.7);
    max-width: 100%;
    max-height: 100%;
    overflow-y: auto;
    
    #voucher-content{
        padding: 1em;
        text-align: center;
        
        h2{ margin-top: 0; }
        
        img{
            margin-bottom: 1em;
            max-width: 100%;
        }
        select{
            margin-bottom: 0.4em;
            
            &:first-of-type{
                margin-right: 0.5em;
            }
        }
        
        #id_quantity{
            width: 30px;
        }
        
        .voucher-button{
            color: #f1f1f1;
            background: rgb(171,0,7);
            cursor: pointer;
            border: none;
            margin: 1em;
            padding: 0.5em;
            transition: color 0.2s;
            transition: background 0.2s;
            
            &[disabled]{
                background: rgb(136,136,136);
                color: #ccc;
                cursor: auto;
            }
        }
    }
    
    #card-image-wrapper{ position: relative; }
    
    #gift-card-text{
        position: absolute;
        bottom: 11%;
        left: 50%;
        width: 70%;
        text-align: center;
        color: #333333;
        font-family: 'Roboto Condensed', sans-serif;
        font-weight: bold;
        font-size: 0.8em;
        -webkit-transform: translateX(-50%);
        -moz-transform: translateX(-50%);
        -o-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        transform: translateX(-50%);
        
        p{ margin: 0; }
        
        a{
            color: #333333;
        }
    }
}
    
//current iPhone rules. We'll want to depricate these at some point in favor of new responsive styles
body.isPhone #voucher-wrapper{
    height: 100%;
    
    input, select,
    #voucher-content .voucher-button{
        display: block;
        margin: 1em auto;
        text-align: center;
    }
}

@media only screen and (max-width: 1469px) {
    #voucher-wrapper #voucher-content select:first-of-type{ margin-right: 0; }
}

@media only screen and (max-width: 440px) {
    .loadingScreen{
        #loading-overlay{
            #loading-content{
                width: auto;
                top: 0;
                .translate(0,0);
                
                .centered-img{
                    width: 100%;
                }
            }
        }
    }
}

