@thumbnail-default-width: 80px;
@thumbnail-default-height: 80px;
@thumbnail-medium-width: 120px;
@thumbnail-medium-height: 120px;
@thumbnail-large-height: 160px;
@thumbnail-xl-height: 240px;

@palette-size: 60px;

//default layout - 6 column for most thumbnails, but 4 column for medium (config) thumbnails
#sections-content{
 width: @thumbnail-default-width * 6 + @scrollbar-buffer-size + (@default-margin * 2);
}
.thumbnail-button{
    &.w80h80{
        img{
            width: @thumbnail-default-width;
            height: @thumbnail-default-height;
        }
    }
    &.w120h120{
        img{
            width: @thumbnail-medium-width;
            height: @thumbnail-medium-height;
        }
    }
    &.w80h160{
        img{
            width: @thumbnail-default-width;
            height: @thumbnail-large-height;
        }
    }
    &.w80h240{
        img{
            width: @thumbnail-default-width;
            height: @thumbnail-xl-height;
        }
    }
    //currently this only applies to 2h weapons in heroforge
    &.w30h30{
        img{
            width: 30px;
            height: 30px;
        }
        &.no-image{
            width: 30px;
            height: 30px;
            cursor: default;
        }
        &.no-image img{
            display: none!important;
        }
    }
}

.specified-columns{
    .justify-content(center);
}

.generate-columns(6);
.generate-columns(@n, @i: 1) when (@i =< @n) {
  .specified-columns-@{i}.thumb-width-80 {
    width: @i*@thumbnail-default-width;
    margin: 0 auto;
  }
  .specified-columns-@{i}.thumb-width-120 {
    width: @i*@thumbnail-medium-width;
    margin: 0 auto;
  }
  .generate-columns(@n, (@i + 1));
}

//at 1680 we'll do 5 columns at 80 and resize 120s to 100 (5 * 80 = 400 so 4 column for the larger ones)
@media only screen and (max-width: 1680px) {
    #sections-content{
     width: @thumbnail-default-width * 5 + @scrollbar-buffer-size + (@default-margin * 2);
    }
    .thumbnail-button{
        &.w80h80{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-default-height;
            }
        }
        &.w120h120{
            img{
                width: 100px;
                height: 100px;
            }
        }
        &.w80h160{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-large-height;
            }
        }
        &.w80h240{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-xl-height;
            }
        }
    }
    .generate-columns(6);
    .generate-columns(@n, @i: 1) when (@i =< @n) {
      .specified-columns-@{i}.thumb-width-80 {
        width: @i*@thumbnail-default-width;
        margin: 0 auto;
      }
      .specified-columns-@{i}.thumb-width-120 {
        width: @i*100px;
        margin: 0 auto;
      }
      .generate-columns(@n, (@i + 1));
    }
}

//at 1440 we'll do 4 columns at 80
//120 wide images will have extra padding but gender-race needs to always be even number of columns so 2 column is fine
@media only screen and (max-width: 1440px) {
    #sections-content{
     width: @thumbnail-default-width * 4 + @scrollbar-buffer-size + (@default-margin * 2);
    }
    .thumbnail-button{
        &.w80h80{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-default-height;
            }
        }
        &.w120h120{
            img{
                width: @thumbnail-medium-width;
                height: @thumbnail-medium-height;
            }
        }
        &.w80h160{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-large-height;
            }
        }
        &.w80h240{
            img{
                width: @thumbnail-default-width;
                height: @thumbnail-xl-height;
            }
        }

        &.paint-preconf{
            &.w120h120{
                width: @palette-size * 0.75;
                height: @palette-size * 0.75;
            }
        }
    }
    .generate-columns(6);
    .generate-columns(@n, @i: 1) when (@i =< @n) {
      .specified-columns-@{i}.thumb-width-80 {
        width: @i*@thumbnail-default-width;
        margin: 0 auto;
      }
      .specified-columns-@{i}.thumb-width-120 {
        width: @i*@thumbnail-medium-width;
        margin: 0 auto;
      }
      .generate-columns(@n, (@i + 1));
    }
}

//at 1024 we reduce the 80 wides to 60 wide, but preserve the 120s
//a 4 column grid for 80s (now 60s) becomes a 2 column grid for 120s
@media only screen and (max-width: 1024px) {
    
    #sections-content{
     width: (@thumbnail-default-width * 0.75) * 4 + @scrollbar-buffer-size + (@default-margin * 2);
    }
    .thumbnail-button{
        &.w80h80{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-default-height * 0.75;
            }
        }
        &.w120h120{
            img{
                width: @thumbnail-medium-width;
                height: @thumbnail-medium-height;
            }
        }
        &.w80h160{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-large-height * 0.75;
            }
        }
        &.w80h240{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-xl-height * 0.75;
            }
        }
    }
    .generate-columns(6);
    .generate-columns(@n, @i: 1) when (@i =< @n) {
      .specified-columns-@{i}.thumb-width-80 {
        width: @i*@thumbnail-default-width * 0.75;
        margin: 0 auto;
      }
      .specified-columns-@{i}.thumb-width-120 {
        width: @i*@thumbnail-medium-width;
        margin: 0 auto;
      }
      .generate-columns(@n, (@i + 1));
    }
}

//900 will be our smallest size where we reduce everything by 75% (80->60, 120->90)
//3*60 = 180, 2*90=180
@media only screen and (max-width: 900px) {
    
    #sections-content{
     width: (@thumbnail-default-width * 0.75) * 3 + @scrollbar-buffer-size + (@default-margin * 2);
    }
    .thumbnail-button{
        &.w80h80{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-default-height * 0.75;
            }
        }
        &.w120h120{
            img{
                width: @thumbnail-medium-width * 0.75;
                height: @thumbnail-medium-height * 0.75;
            }
        }
        &.w80h160{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-large-height * 0.75;
            }
        }
        &.w80h240{
            img{
                width: @thumbnail-default-width * 0.75;
                height: @thumbnail-xl-height * 0.75;
            }
        }
    }
    .generate-columns(6);
    .generate-columns(@n, @i: 1) when (@i =< @n) {
      .specified-columns-@{i}.thumb-width-80 {
        width: @i*@thumbnail-default-width * 0.75;
        margin: 0 auto;
      }
      .specified-columns-@{i}.thumb-width-120 {
        width: @i*@thumbnail-medium-width * 0.75;
        margin: 0 auto;
      }
      .generate-columns(@n, (@i + 1));
    }
}

//specific styles for two hands pane
.specified-columns-3{
    .flexbox();
    .flexbox-align-items(center);
    .flex-wrap(wrap);
}