#loading-overlay{
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 20;
    background: #f1f1f1;
    width: 100%;
    height: 100%;
    visibility:visible;
    opacity:1;
    .transition-delay(0s);
    
    &.hide{
        visibility: hidden;
        opacity: 0;
        .transition(visibility 0s linear 0.2s,opacity 0.2s linear, top 0.2s linear);
    }
    
    #loading-content{
        color: #333;
        text-align: center;
        display: block;
        width: 440px;
        margin: 0 auto;
        position: relative;
        top: 50%;
        
        .translate(0%, -50%);
        
        a{
            color: @brand-interactive;
        }
    }
}