.linked-part{
    text-align: center;
    > *{
        vertical-align: middle;
    }
    .link-button{
        cursor: pointer;
        opacity: 0;
        .transition-property(opacity);
        .transition-duration(0.3s);
        .transition-timing-function(ease);
    }
    &:hover{
        .link-button{
            opacity: 1;
        }
    }
}