//heroforge specific styles
#header{
    background: @brand-primary;
    color: #f1f1f1;
    font-size: 0.8em;
    
    .content{
        padding: 0.5em 20px 0.5em 15px;
        .flexbox();
        justify-content: space-between;
        
        #user{
            a{
                margin-left: 1em;
                cursor: pointer;
                text-decoration: none;
            }
            #open-cart{
                img{
                    vertical-align: top;
                }
            }
        }
    }
}

footer{
    position: absolute;
    left: 0px;
    bottom: 0px;
    width: 100%;
    color: #444;
    background: rgba(0,0,0,0.3);
    background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,0.4) 99%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0)), color-stop(99%,rgba(0,0,0,0.4))); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.4) 99%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.4) 99%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.4) 99%); /* IE10+ */
    background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,0.4) 99%); /* W3C */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#66000000',GradientType=0 ); /* IE6-9 */

    text-transform: uppercase;
    font-size: 0.7em;
    
    .float-left{
        padding: 1.7em 1em 0.7em 0.7em;
    }
    .float-right{
        position: absolute;
        right: 0.7em;
        bottom: 0.7em;
    }
}

//extra padding at bottom of editor to give footer space
#editor #sections-wrapper #print-my-mini{
    margin-bottom: 2em;
}

//styles for the saved characters list
#my-characters-wrapper{
    position: relative;
    margin-top: 164px;
    margin-bottom: 170px;
    width: 291px;
    margin-left: 15px;
    pointer-events: none;
    
    #my-characters-content{
        position: absolute;
        bottom: 0px;
        background: rgba(0,0,0,0.7);
        width: 100%;
        overflow: auto;
        max-height: 100%;
        pointer-events: auto;
        color: #f1f1f1;
        
        //transitions
        &.my-saves-enter {
          opacity: 0.01;
          bottom: -2em;
        }
        
        &.my-saves-enter.my-saves-enter-active {
          opacity: 1;
          bottom: 0em;
          .transition(opacity 200ms ease-out, bottom 0.2s ease-out);
        }
        
        &.my-saves-leave {
          opacity: 1;
          bottom: 0em;
        }
        
        &.my-saves-leave.my-saves-leave-active {
          opacity: 0.01;
          bottom: -2em;
          .transition(opacity 200ms ease, bottom 0.2s ease);
        }
        
        .presets{
            width: 250px; //config thumbs are 120w with a 10px margin so this is 2 column
            margin: 10px auto;
            
            .savedConfigThumbnail:nth-child(2n) {
                margin-left: 10px;
            }
        }
    }
}

//material scale view
.layout-panel.Material{
    background: rgba(0, 0, 0, 0.7);
    border: 0.3em solid rgba(255,255,255,0.2);
    padding: 0em;
    text-align: left;
    
    h4{
        display: inline-block;
        margin: 0.3em 0em 0em 0.3em;
    }
    .parts{
        padding-right: 0.3em;
    }
    p{
        font-size: 0.8em;
        margin-right: 0.5em;
        margin-left: 0.5em;
        line-height: 1.4em;
    }
    img{
        max-width: 100%;
    }
    #product-preview{
        text-align: center;
    }
}

//media queries
@media only screen and (max-width: 900px) {
    
    #header .content {
        padding: 0.5em 20px 0.5em 12px;
    }
    #my-characters-wrapper{
        margin-top: 124px;
    }
}

@media only screen and (max-width: 768px){
    
    #header .content {
        padding: 0.5em 20px 0.5em 8px;
    }
    #my-characters-wrapper{
        margin-top: 84px;
        margin-bottom: 140px;
    }
}

@media only screen and (max-width: 650px) {
    #my-characters-wrapper{
        margin-bottom: 135px;
        width: 211px;
        font-size: 0.8em;
        #my-characters-content .presets{
            width: 170px;
        }
    }
}

@media only screen and (max-height: 500px){
    
    #header{
        font-size: 0.5em;
        img{
            height: 8px;
        }
        .content{
            padding: 0.5em 20px 0.5em 4px;
        }
    }
    #my-characters-wrapper{
        margin-top: 44px;
        margin-bottom: 105px;
        width: 211px;
        font-size: 0.8em;
        #my-characters-content .presets{
            width: 170px;
        }
    }
}


@media only screen and (max-width: 850px){
    footer{
        font-size: 0.6em;
    }
}
@media only screen and (max-width: 740px){
    footer{
        font-size: 0.5em;
    }
}

