//site wrapper positioned to fill the page
.fullscreen-flex{
    .flexbox();
    width: 100%;
    height: 100%;
}
//flexbox class rules
.flex-column{
    .flex-direction(column);
}
.flex-row{
    .flex-direction(row);
}
#app-wrapper{
    position: relative;
    display: block;
    flex: 2;
    overflow: hidden;
    bottom: 0;
    left: 0;
    background: #f1f1f1;
}

//canvas fills the wrapper
#canvasWrapper,
#character-canvas{
    position: relative;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    overflow: hidden; //this shouldn't be needed. canvas should fill this div instead of the window size
}
    #canvasWrapper canvas{
        cursor: move;
    }

//site elements are positioned absolutely within the wrapper
#noWebGL{
    text-align: center;
    position: relative;
    top: 48%;
}
    #noWebGL a{ color: rgba(34, 34, 34, 0.7); }
    #noWebGL a:hover{ color: rgba(34, 34, 34, 0.9); }

#msCounter{
    /*display: none;*/
    position: absolute;
    left: 0px;
    bottom: 0px;
    color: #fff;
}

.layout-panel-wrapper{
    .flexbox();
    .flex-direction(column);
    width: 100%;
}

.layout-panel{
    // width: 100%;
    background: rgba(0,0,0,0.5);
    .flexbox();
    .flex-direction(column);
    padding: 10px;
    .flex-basis(auto);
    .flex-grow(0);
    overflow: auto;

    &.pinned{
        .flex-shrink(0);
        max-height: 50%;
        background: rgba(0,0,0,0.8);
    }
}

//additional rules
.mirrored{
    .scaleX(-1);
}
.centered-img{
    position: relative;
    left: 50%;
    .translate(-50%, 0%);
    
    &.shrink-fill{
        max-width: 100%;
    }
}

//basically the error message page
.loadingScreen{
    #app-wrapper{
        min-width: auto;
        min-height: auto;
    }
}