#tabs{
    /*width: 30%;*/
    width: 135px;
    text-align: right;
    font-size: 1em;
    text-transform: capitalize;
    font-weight: 200;

    ul{
        margin: 0 0.3em 0 0;
    }
    //individual tab elements
    li{
        padding: 0.5em;
        background: rgba(0,0,0,0.5);
        margin-bottom: 0.3em;
        pointer-events: auto;
        
        //hover and active state
        &:hover, &.active{
            background: rgba(0,0,0,0.7);
            color: #fff;
        }
        //specific tab cases
        &#genresTab{
            white-space: nowrap;
            text-align: right;
            float: right;
            min-width: 100%;
        }

        img{
            float: left;
        }
    }   
}

@media only screen and (max-width: 768px) {
    #tabs{
        font-size: 0.75em;
        width: 85px;
        
        img{
            height: 18px;
        }
    }
}

@media only screen and (max-height: 500px) {
    #tabs{
        font-size: 0.5em;
        width: 85px;
        
        img{
            height: 12px;
        }
    }
}