@import "thumbnail-sizes-responsive.less";

#editor{
    position: absolute;
    right: 0px;
    top: 0px;
    /*width: 29%;*/
    /*min-width: 295px;*/
    height: 100%;
    color: #ddd;
    .user-select(none);
    pointer-events:none;
    background:none !important;
    .flexbox();
    
    //editor-wide styles
    img{
        -webkit-user-drag: none; /* Prevents dragging of images/divs etc */
        user-drag: none;
    }
    ul{
        list-style: none;
        padding: 0;
    }
    li{ cursor: pointer; }
    
    select{
        margin-bottom: 0.8em;

        &:last-child{
            margin-bottom: 0;
        }
    }
    
    .editor-main{
        .flexbox();
        margin: 1em;
    }
    
    #sections-wrapper{
        .flexbox();
        .flex-direction(column);
        
        //temp test style until we get responsive design in
        #sections-content{
            //width: @thumbnail-default-size * 5 + @scrollbar-buffer-size + (@default-margin * 2);
            text-align: center;
            min-height: 160px;
            opacity: 1;
            -webkit-transition: opacity 0.1s ease;
            -moz-transition: opacity 0.1s ease;
            -o-transition: opacity 0.1s ease;
            transition: opacity 0.1s ease;
            pointer-events: auto;
            .flexbox();
            .flex(2);

            //still have overflow when we have a single submenu, like pose
            .single-submenu{
                // overflow: auto;
                //changing this to use flex instead of overflow
                //overflow has been moved to layout-panel
                .flexbox();
            }
        }
        //why didn't this work with nesting???
        #sections-content > div{
            //make sure the child div of the sections wrapper fill the width
            .flex(2);
            
            &.flex-fill{
                .flex(2);
            }
        }
        
        #print-my-mini{
            text-align: center;
            .shop-button{
                display: inline-block;
                color: #f1f1f1;
                background: @brand-interactive;
                cursor: pointer;
                border: none;
                margin: 1em auto;
                padding: 0.5em;
                
                &#add{
                    width: 60%;
                    min-width: 201px;
                    pointer-events: auto;
                }
            }
            p.error{
                display: block;
                margin: 0;
                font-size: 0.8em;
                position: absolute;
                color: @brand-interactive;
            }
        }
    }
    
    //components
    @import "accordion.less";
    @import "slider.less";
    @import "linked-parts.less";
    @import "thumbnail-button.less";
    @import "buttons.less";
    //tabs
    @import "tabs.less";
}

//media queries
@media only screen and (max-height: 600px) {
    #editor #sections-wrapper{
        font-size: 0.8em;
    }
}
@media only screen and (max-height: 500px) {
    #editor #sections-wrapper{
        font-size: 0.6em;
    }
}
@media only screen and (max-height: 360px) {
    #editor #sections-wrapper{
        font-size: 0.5em;
    }
}