.accordion{
    .flexbox();
    .flex-direction(column);
    
    .accordion-header{
        position: relative;
        display: block;
        margin-top: 0.3em;
        overflow: hidden;
        background: rgba(0,0,0,0.5);
        .flex-basis(auto);
        .flex-grow(0);
        .flex-shrink(0);
        
        &:first-child{
            margin: 0;
        }
        
        h3{
            cursor: pointer;
            margin: 0;
            .transition(all 0.15s ease);
        }
        
        .clear-button{
            cursor: pointer;
            position: absolute;
            right: 0.5em;
            top: 0px;
            opacity: 0;
            height: 100%;
            font-size: 0.8em;
            .flexbox();
            .flexbox-align-items(center);
            
            a{
                &:hover{
                    .scale(1.2);
                }    
            }
        }
        
        .arrow-wrapper{
            height: 100%;
            position: absolute;
            top: 0;
            left: 1em;
            .flexbox();
            .flexbox-align-items(center);
            
            .arrow {
                .transition(all 0.3s ease);
                .arrow-right(3px, @brand-interactive);
            }
        }
        
        //Accordion header hover state
        &:hover{
            background: rgba(0,0,0,0.7);
            .clear-button{
                opacity: 1;
            }
        }
        //accordion header open state
        &.open{
            background: rgba(0,0,0,0.7);
            h3{
                font-size: 1.4em;
            }
            .clear-button{
                opacity: 1;
            }
            .arrow {
                .rotate(90deg);
            }
        }
    }
    
    .accordion-content{
        .flexbox();
        .flex(0);
        overflow-y: auto;
        .transition(all 0.3s ease);
        //Accordion fold open state
        &.open{
            .flex-basis(auto);
        }
    }
    
}