//#character-area encompases the entire character menu
#character-area{
    position: absolute;
    left: 15px;
    bottom: 3em;
    
    a{
        color: rgba(0,0,0,0.5);
        .transition( color 0.2s ease );
        
        &:hover{ color: rgba(0,0,0,0.7); }
    }
    
    //links
    > div a{
        cursor: pointer;
        margin-right: 1em;
        
        img, .icon{
            opacity: 0.5;
            .transition( opacity 0.2s ease, transform 0.2s ease );
        }
        
        &:hover{
            img, .icon{  opacity: 0.7; }
        }
    }
    
    //state icons. possibly depricate these with react?
    #saving-icon{ display: none; }
    &.saving #save-icon{display: none; }
    &.saving #saving-icon{ display: inline; }
    
    //name input
    #namecard{
        font-size: 2.5em;
        text-transform: capitalize;
        font-family: 'Roboto Condensed', sans-serif;
        background: none;
        outline: none;
        border: none;
        padding: 0;
        margin: 0;
        color: rgba(0,0,0,0.5);
    }
    
    //error message
    .no-name-error{
        margin: 0;
        color: @brand-interactive;
        font-size: 0.8em;
        opacity: 0;
        margin-top: -0.8em;
        .transition( opacity 0.5s ease );
    }
    &.no-name .no-name-error{
        opacity: 1;
        .transition-duration(0.1s);
    }
}




//character-wrapper is specifically for the list of characters in the load character menu
#character-wrapper{
    color: #f1f1f1;
    text-transform: capitalize;
    
    ul{
        list-style: none;
        padding: 0;
    }
    
    li{
        cursor: pointer;
        &:hover{ color: #fff; }
    }
    
    a{
        color: #f1f1f1;
        text-transform: capitalize;
        &:hover { color: fade(#f1f1f1, 70%); }
    }
    
    #characterList{
        margin: 10px;
        //li refers to the element wrapping a character listing
        li{
            text-align: center;
            position: relative;
            display: block;
            max-width: 120px;
            display: inline-block;
            margin-bottom: 2.2em;
            
            /*every even child*/
            &:nth-child(2n){
                margin-left: 10px;
            }
            //x in corner to delete the character
            .deleteCharacter{
                color: #4d4d4d;
                position: absolute;
                top: 0.2em;
                right: 0.2em;
                display: none;
                font-size: 2em;
                line-height: 0.5em;
                .transition(none);
            }
            //thumbnail
            img{
                width: 120px;
                display: block;
            }
            .character-name{
                display: block;
                position: absolute;
                width: 100%;
                font-size: 0.8em;
            }
            //STATE CLASSES
            //hovering the X to delete the character
            &.deleteHover{
                background: @brand-interactive;
                
                .character-name{
                    text-decoration: line-through;
                }
                .loadCharacter{
                    opacity: 0.4;
                }
                .deleteCharacter{
                    color: #f1f1f1;
                }
            }
            &:hover{
                .deleteCharacter{
                    display: block;
                }
            }
        }
        a{
            margin: 0;
        }
    }
}


//screenshot, my characters, and share behavioral styles
//allows for popouts to come from these icons
//THIS IS ALL STILL PRETTY MESSY LET'S CLEAN IT LATER
//mixins
.icon-behavior(){
    display: inline-block;
    width: 21px;
    height: 0px;
    padding-top: 16px;
    background: url(../img/icon_screenshot.png);
    overflow: hidden;
    position: relative;
    background-repeat: no-repeat;
    vertical-align: bottom;
}

.popout-wrapper(){
    position: absolute;
    width: 330%;
    background: rgba(0,0,0,0.7);
    color: #f1f1f1;
    left: 50%;
    bottom: 0.5em;
    opacity: 0;
    visibility: hidden;
    .translate(-50%, 0%);
}
//end mixins//

#screenshot{
    .icon{
        .icon-behavior();
    }
    &:hover{
        .icon{ opacity: 0.7; }
    }
}


#load-icon{
    .icon-behavior();
    background: url(../img/icon_characters_spritesheet.png);
    width: 26px;
    vertical-align: middle;
}
    
//share button and popout
#share{
    display: inline-block;
    position: relative;
    
    .share-url-wrapper{
        .popout-wrapper();
        visibility: visible;
        opacity: 1;
        bottom: 2em;
        
        .share-url-content{
            padding: 1em;
            text-transform: none;
            text-align: center;
            
            input{ width: 100%; }
        }
    }
    
    &.open{
        
        a{
            color: rgba(0,0,0,0.7);
            
            img{
                opacity: 0.7;
                .rotate(90deg);
            }
        }
        
        .icon{
            opacity: 0.7;
            .rotate(90deg);
        }
    }
    
    .icon{
        cursor: pointer;
        display: inline-block;
        width: 15px;
        height: 0px;
        padding-top: 16px;
        background: url(../img/icon_share.png);
        overflow: hidden;
        background-repeat: no-repeat;
        opacity: 0.5;
        vertical-align: middle;
        position: relative;

        .rotate(0deg);
        .transition(transform 0.2s ease);
    }
        
    &:hover{
        .icon{ opacity: 0.7; }
    }
}




//characters button and list
#my-characters{
    display: inline-block;
    position: relative;
    
    &.open{
        > a{
            color: rgba(0,0,0,0.7);
            
            #load-icon {
              opacity: 0.7;
              background-position: -26px 0px;
            }
        }
    }
}

//character thumbnails inside the "my characters" list
.savedConfigThumbnail{
    height: auto;
    text-align: center;
    position: relative;
    display: inline-block;
    margin-bottom: 2.2em;
    cursor: pointer;
    
    .thumbnail-button{
        display: block;
        width: 120px;
        .transition(background-color 0.3s ease);
        background-color: fade(@brand-interactive, 0%);
        
        img{
            width: 120px;
            height: 120px;
            display: block;
            opacity: 0.5;
            .transition(all 0.3s ease);
        }
    }
    .character-name{
        display: block;
        width: 100%;
        font-size: 0.8em;
        position: absolute;
        color: #f1f1f1;
        text-transform: capitalize;
    }
    .delete-character{
        color: rgb(77, 77, 77);
        position: absolute;
        top: 0.2em;
        right: 0.2em;
        display: none;
        font-size: 2em;
        line-height: 0.5em;
    }
    
    //hover state
    &:hover{
        .delete-character{
            display: block;
        }
        .thumbnail-button{            
            img{
                opacity: 1;
            }
        }
    }
    
    //state styles for when we're hovering over the delete button
    &.delete-hover{
        .thumbnail-button{
            background-color: @brand-interactive;
            
            img{
                opacity: 0.4;
            }
        }
        .character-name{
            text-decoration: line-through;
            color: fade(#f1f1f1, 50%);
        }
        
        .delete-character{
            color: #f1f1f1;
        }
    }
}

@media only screen and (max-width: 768px) {
    #character-area{
        font-size: 0.8em;
    }
}

@media only screen and (max-width: 650px) {
    #character-area{
        font-size: 0.6em;
        
        img{ height: 12px; }
        #load-icon{
            padding-top: 12px;
            width: 20px;
            background-size: 39px 12px;
        }
        #share .icon{
            padding-top: 12px;
            width: 12px;
            background-size: 12px 12px;
        }
    }
    
    #my-characters.open > a #load-icon {
        background-position: -19px 0px;
    }
}

@media screen and (max-width: 650px) , screen and (max-height: 500px) {
    .savedConfigThumbnail{
        .thumbnail-button{
            width: 80px;
            
            img{
                width: 80px;
                height: 80px;
            }
        }
    }
}